/*  dropdown  */


/* The root */
/* The root */
.p-nested-dropdowns {
    /* Border */
    display: flex;

    /* Reset list styles */
    list-style-type: none;
    margin: 0;
    z-index: 3;
    background-color: #fff;
    padding: 0;

}

.p-nested-dropdowns li {
    /* Spacing */
    text-transform: uppercase;
    cursor: pointer;
    /* Used to position the sub dropdown */
    position: relative;
}



/* The sub dropdown */
.p-nested-dropdowns ul {
    /* Border */

    box-shadow: 0px 0px 10px -7px #2e81f5;
    background-color: #fff;


    /* Hidden by default */
    display: none;

    /* Absolute position */
    left: 0;
    position: absolute;
    top: 100%;

    /* Reset styles */
    list-style-type: none;
    margin: 0;
    padding: 0;

    /* Width */
    width: 200px;
}

/* The second level sub dropdown */
.p-nested-dropdowns ul ul {
    left: 100%;
    position: absolute;
    top: 0;
}

/* Change background color of list item when being hovered */
.p-nested-dropdowns li:hover {
    background-color: #f8f8f8;
    color: #2e81f5;
}

/* Show the direct sub dropdown when hovering the list item */
.p-nested-dropdowns li:hover > ul {
    display: block;
    color: #555;
}
